import React from 'react';

import { Modal, Button, Row, Col, Form } from 'react-bootstrap';

const DeleteModal = (props) => {

    const [cancelData, setCancelData] = React.useState({
        reason: '',
    });

    const [errors, setErrors] = React.useState({})

    const _cancelDataChange = (e) => {
        setErrors({ ...errors, [e.target.name]: '' })
        setCancelData({ ...cancelData, reason: e.target.value });
    }

    const _done = (e) => {

    }


    return (
        <Modal
            style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
            show={props.show}
            onHide={props.onCancel}
        >
            <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Invoice Cancelation</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >
                <Row>
                    <Col md={1}>
                    </Col>
                    <Col md={10}>
                        <Form.Group  >
                            <Form.Label>Reason of Cancelation*</Form.Label>
                            <Form.Control name="reason" value={cancelData.reason} onChange={_cancelDataChange} type="text" as = 'textarea' row = "3" placeholder="Cancelation Reason" />
                        </Form.Group>
                    </Col>
                    <Col md={1}>
                    </Col>
                </Row>
            </Modal.Body>


            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                <Button variant="secondary" onClick={props.onCancel}>
                    <b>Cancel</b>
                </Button>
                {cancelData.reason ?
                    <Button variant="danger" onClick={() => props.onDelete(cancelData)}>
                        <b>Done</b>
                    </Button> :
                    null
                }

            </div>
        </Modal >
    )

}

export default DeleteModal;