



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge, Modal, Button } from 'react-bootstrap';
import { NavLink } from 'react-router-dom'
import Loader from '../../App/layout/Loader/index'
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import IsEmpty from '../../App/components/Reusables/IsEmpty'
import CancelModal from './CancelModal';
import Num from '../../App/components/Reusables/Num';
import { deleteInvoices, getInvoice, updateInvoice, exportBill, cancelInvoice } from '../../api/sales/invoice';
import { formatNumber } from '../../helpers/formatNumber';
import isEmpty from '../../validation/isEmpty';
import CreatePaymentModal from '../Payments/CreatePaymentModal';
import { getPayment, deletePayment } from '../../api/payment/payment';
import Payments from '../Payments/Payments';
import DEMO from '../../globals/constant';
import PaymentInfo from '../../App/components/Reusables/PaymentInfo';




const Invoice = (props) => {


    const [loading, setLoading] = React.useState(true);
    const [invoiceData, setInvoiceData] = React.useState({});
    const [invoiceItemsData, setInvoiceItemsData] = React.useState([]);
    const [createPaymentData, setCreatePaymentData] = React.useState({ showModal: false, creditPaymentFor: null })
    const [paymentsArr, setPaymentsArr] = React.useState([])
    const [cancelData, setCancelData] = React.useState({ showModal: false, invoiceId: null })

    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getInvoice({ action: 'get', invoice_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        await setInvoiceData({ ...res.json.invoice[0] });
                        await setInvoiceItemsData([...res.json.invoice_items]);
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });

            const paymentJson = {
                "action": "get",
                "filter": "invoice",
                "invoice": parseInt(props.match.params.id),
            }
            console.log(JSON.stringify(paymentJson))
            await getPayment(paymentJson, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        await setPaymentsArr([...res.json.payments])
                        ladder++
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
            if (ladder === 2) {
                setLoading(false)
            }
        })()
        return () => controller.abort();
    }, [])


    const getItemTotal = (anItem) => {
        //check if all of required field are filled
        const isAllFilled = !isEmpty(anItem.price) && !isEmpty(anItem.quantity);

        if (isAllFilled) {
            //calculate tax amt
            let taxAmt = 0;
            if (anItem.taxes) {
                for (let aTax of anItem.applied_tax) {

                    if (aTax.tax_type === 'fixed') {
                        taxAmt += aTax.rate;
                    } else {
                        taxAmt += anItem.price * aTax.rate * 0.01;
                    }
                }
            }

            //calculate discount amt
            let discountAmt = 0;
            if (anItem.discount) {
                for (let aDiscount of anItem.applied_discount) {
                    if (aDiscount.discount_type === 'fixed') {
                        discountAmt += aDiscount.rate;
                    } else {
                        discountAmt += anItem.price * aDiscount.rate * 0.01;
                    }
                }
            }

            //calculate items total cost
            const itemTotal = anItem.quantity * (anItem.price + taxAmt - discountAmt)

            return {
                totalDiscount: anItem.quantity * discountAmt,
                totalTax: anItem.quantity * taxAmt,
                itemTotal,
            };
        } else return 0;
    }


    const _triggerDeleteModal = (e) => {
        setCancelData({ showModal: true, invoiceId: props.match.params.id });
    }
    const _payForCredit = (payment) => {
        setCreatePaymentData({ ...createPaymentData, showModal: true, creditPaymentFor: payment.id, creditPaymentAmount: payment.amount })
    }

    //here delete corresponds to cancel of invoice cause invoice can only be canceled
    const _delete = (data) => {
        const requestJson = {
            "action": "cancel",
            "invoice_id": parseInt(cancelData.invoiceId),
            "reason": data.reason,
        }
        console.log(JSON.stringify(requestJson))
        cancelInvoice(requestJson)
            .then(async (res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Invoice canceled successfully!', background: '#dffff0' })
                    props.history.push('/invoice/' + cancelData.invoiceId)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
    }

    const _markSent = async () => {
        const invoiceUpdateJson = {
            'action': 'update',
            'invoice_id': invoiceData.id,
            'customer': invoiceData.customer,
            'invoiced_on': invoiceData.invoiced_on,
            'due_on': invoiceData.due_on,
            'additional_discount': invoiceData.additional_discount,
            'total_weight': invoiceData.total_weight,
            'weight_unit': invoiceData.weight_unit,
            'is_sent': true,
            'status': invoiceData.status,
        }
        await updateInvoice({ ...invoiceUpdateJson })
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Invoice marked sent!', background: '#dffff0' })
                    setInvoiceData({ ...invoiceData, is_sent: true })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err);
            });
    }
    const _print = () => {
        const requestJson = {
            "action": "export",
            "invoice": parseInt(props.match.params.id),
        }
        exportBill(requestJson, null, 'print')
            .then(async (res) => {
                if (res.json.status) {
                    const myBlob = res.json.blob
                    var objectURL = await URL.createObjectURL(myBlob);
                    document.querySelector('#pdf-frame').src = '';
                    document.querySelector('#pdf-frame').src = objectURL;
                    objectURL = await URL.revokeObjectURL(myBlob)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .then(
                function () {
                    window.setTimeout(function () {
                        document.querySelector('#pdf-frame').contentWindow.print();
                    }, 100)

                }).catch((err) => {
                    console.log(err);
                });
    }
    const _export = () => {
        const requestJson = {
            "action": "export",
            "invoice": parseInt(props.match.params.id),
        }
        exportBill(requestJson, null, 'export')
            .then(async (res) => {
                if (res.json.status) {
                    const blob = res.json.blob
                    const contentType = res.response.headers.get("content-type");
                    const url = window.URL.createObjectURL(new Blob([blob]));
                    const link = document.createElement('a');
                    link.href = url;
                    let format = '.pdf';
                    if (contentType === 'application/ms-excel') format = '.xlsx';
                    link.setAttribute('download', `invoice-${invoiceData.invoice_number}` + format);
                    document.body.appendChild(link);
                    link.click();
                    link.parentNode.removeChild(link);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err);
            });
    }

    let invoiceSubTotal = 0;
    let totalOfItemSubtotal = 0;
    const InvoiceItemsList = invoiceItemsData.map((item, index) => {


        let { totalDiscount, totalTax, itemTotal } = getItemTotal(item);
        invoiceSubTotal += itemTotal
        itemTotal = formatNumber(itemTotal)
        totalOfItemSubtotal += item.price * item.quantity
        console.log(totalOfItemSubtotal)

        return (
            <tr
                key={index}>
                <th scope="row">{item.id}</th>

                <td>{item.item_name}</td>
                <td>Rs. <Num>{item.price}</Num></td>
                <td><Num>{item.quantity}</Num></td>
                <td>Rs. <Num>{item.price * item.quantity}</Num></td>
                <td>Rs. <Num>{totalDiscount}</Num></td>
                <td>Rs. <Num>{totalTax}</Num></td>
                <td>Rs. <Num>{itemTotal}</Num></td>
            </tr>
        )
    });




    const dueAmount = invoiceData.bill_amount - invoiceData.paid_amount;
    console.log('due amount is ', dueAmount);

    const dueOn = (new Date(invoiceData.due_on)).toDateString()
    const invoicedOn = (new Date(invoiceData.invoiced_on)).toDateString()
    let paymentStatus = 'Not paid';
    if (invoiceData.paid_amount > 0 && invoiceData.paid_amount < invoiceData.bill_amount) paymentStatus = 'Partially Paid'
    if (dueAmount <= 0) paymentStatus = 'Paid'
    if (loading) return <Loader />

    const isCanceled = invoiceData.is_canceled;
    return (
        <>

            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >{invoiceData.invoice_number}</h4>
                <div>
                    <button
                        disabled={isCanceled}
                        title="You can print original bill only once"
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={_print}
                    > <i className="fa fa-print text-c-white f-16 m-r-5" />Print</button>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={_export}
                    > <i className="fa fa-download text-c-white f-16 m-r-5" />Export</button>
                    <button
                        title="Cancel Invoice"
                        disabled={isCanceled}
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeleteModal}
                    > <i className="fa fa-times text-c-black f-16 m-r-5" />Cancel</button>
                </div>
            </Row>


            <Row >
                <Col md={12}>
                    <Card>
                        <Card.Body >
                            <VerticalTimeline animate={false} layout={'1-column'}>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: Created on ${invoicedOn}`}
                                    iconStyle={{ background: '#12a9f4', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-pencil-square fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Invoice Creation</h5>
                                    <button
                                        onClick={() => props.history.push(`/invoice/update/${props.match.params.id}`)}
                                        disabled={isCanceled || (invoiceData.status_is_sold)}
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                    >
                                        Edit
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: ${invoiceData.is_sent ? `Sent` : 'Not sent Yet'}`}
                                    iconStyle={{ background: '#be81dc', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-envelope fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Send Invoice</h5>
                                    <button
                                        disabled={invoiceData.is_sent || isCanceled}
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                        onClick={_markSent}
                                    >
                                        {invoiceData.is_sent ? `Sent` : 'Mark Sent'}
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: ${paymentStatus}`}
                                    iconStyle={{ background: '#20c997', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-money fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Payment</h5>


                                    <button
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                        disabled={isCanceled || (dueAmount <= 0)}
                                        onClick={() => setCreatePaymentData({ ...createPaymentData, showModal: true })}
                                    >
                                        {dueAmount <= 0 ? 'Paid' : 'Add Payment'}
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                            </VerticalTimeline>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            <Row>
                <Col xl={12}>
                    <Card>
                        <PaymentInfo isCanceled={isCanceled} cancelationReason={invoiceData.cancelation_reason} billAmount={invoiceData.bill_amount} paidAmount={invoiceData.paid_amount} />
                        <Card.Body>

                            <div className="row d-flex align-items-center">
                                <Col md={2}></Col>
                                <Col md={8}>
                                    <div className='mb-1 border-bottom'><b>Customer Name</b></div>

                                    <div className='mt-1 mb-3'>
                                        <NavLink to={`/customer/${invoiceData.customer}`}>
                                            <b>{invoiceData.customer_name} {" "}</b>
                                        </NavLink>

                                    </div>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Invoiced on</b></div>
                                            <div className='mt-1 mb-3'>{(new Date(invoiceData.invoiced_on)).toDateString()}</div>
                                        </div>
                                        <div className="w-50 ml-4">

                                            <div className='mb-1 border-bottom text-right'><b>Due on</b></div>
                                            <div className='mt-1 mb-3 text-right'>{(new Date(invoiceData.due_on)).toDateString()}</div>

                                        </div>
                                    </div>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Invoice Status</b></div>
                                            <div className='mt-1 mb-3'>{invoiceData.status_name}</div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom text-right'><b>Added By</b></div>
                                            <div className='mt-1 mb-3 text-right'>{invoiceData.added_by_name ? invoiceData.added_by_name : 'NA'}</div>
                                        </div>
                                    </div>

                                    <div className="">
                                        <div className='mb-1 border-bottom'><b>Updated</b></div>
                                        <div className='mt-1 mb-3'>{invoiceData.count_revision} times</div>
                                    </div>


                                </Col>
                                <Col md={1}></Col>
                            </div>

                            <Card.Header>
                                <Card.Title as="h5">Invoice Items</Card.Title>
                            </Card.Header>
                            {invoiceItemsData.length > 0 ? (
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            <th>Item Name</th>
                                            <th>Price</th>
                                            <th>Quantity</th>
                                            <th>Sub Total</th>
                                            <th>Discount</th>
                                            <th>Tax</th>
                                            <th>Total</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {InvoiceItemsList}
                                        <tr >
                                            <td></td>
                                            <td colSpan={3}><b>Sub Total</b></td>
                                            <td colSpan={1}><b> Rs. <Num>{totalOfItemSubtotal}</Num></b></td>
                                            <td colSpan={1}><b> - Rs. <Num>{invoiceData.discount_total}</Num></b></td>
                                            <td colSpan={1}><b> + Rs. <Num>{invoiceData.tax_total}</Num></b></td>
                                            <td colSpan={1}><b>Rs. <Num>{invoiceSubTotal}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Extra Discount</b></td>
                                            <td colSpan={1}><b> - Rs. <Num>{invoiceData.additional_discount}</Num></b></td>
                                        </tr>

                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Total</b></td>
                                            <td colSpan={1}><b>Rs. <Num>{invoiceSubTotal - invoiceData.additional_discount}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Paid Amount</b></td>
                                            <td colSpan={1}><b>Rs. <Num>{invoiceData.paid_amount}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Due</b></td>
                                            <td colSpan={1}><b>Rs. <Num>{dueAmount}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Total Weight</b></td>
                                            <td colSpan={1}><b><Num>{invoiceData.total_weight}</Num>{invoiceData.weight_unit}</b></td>
                                        </tr>
                                    </tbody>

                                </Table>
                            ) :
                                <IsEmpty>Purchase Items' list is empty!</IsEmpty>}
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={6}>
                    <Payments
                        paymentsArr={paymentsArr}
                        deletePayment={deletePayment}
                        setPaymentsArr={setPaymentsArr}
                        _payForCredit={_payForCredit}
                    />
                </Col>

                <CreatePaymentModal
                    show={createPaymentData.showModal}
                    invoice={invoiceData}
                    dueAmount={dueAmount}
                    creditPaymentFor={createPaymentData.creditPaymentFor}
                    creditPaymentAmount={createPaymentData.creditPaymentAmount}
                    onHide={() => setCreatePaymentData({ ...createPaymentData, showModal: false, creditPaymentFor: null, creditPaymentAmount: null })}
                />
                <CancelModal
                    show={cancelData.showModal}
                    onCancel={() => setCancelData({ ...cancelData, showModal: false })}
                    onDelete={_delete}
                />


            </Row>
            <iframe id="pdf-frame" style={{ display: 'none' }}></iframe>

        </>
    );
}

export default Invoice;
